import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import { ContactUs } from '../resume/contactpage';

import Modal from 'react-bootstrap/Modal';

function Example() {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <Button variant="light

" onClick={handleShow}>
<i class="bi bi-send"></i> <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-send" viewBox="0 0 16 16">
  <path d="M15.854.146a.5.5 0 0 1 .11.54l-5.819 14.547a.75.75 0 0 1-1.329.124l-3.178-4.995L.643 7.184a.75.75 0 0 1 .124-1.33L15.314.037a.5.5 0 0 1 .54.11ZM6.636 10.07l2.761 4.338L14.13 2.576 6.636 10.07Zm6.787-8.201L1.591 6.602l4.339 2.76 7.494-7.493Z"/>
</svg><span className="hair-me d-lg-inline-block text-muted p-2">Hair Me</span>

       
      </Button>

      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>HIRE ME</Modal.Title>
        </Modal.Header>
        <Modal.Body className='p-3'>
        <h6 className='p-3'>Hi, My name is Rakesh MG and I'm a fullstack engineer.</h6>
        <ContactUs />





        </Modal.Body>
        <Modal.Footer>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default Example;